"use client"

import { useState } from "react"
import { useRouter } from "next/navigation"
import { authAPI, setAuthToken } from "@/lib/api"
import { Loader2, AlertCircle, CheckCircle } from "lucide-react"

export default function AuthForm() {
  const router = useRouter()
  const [mode, setMode] = useState<"login" | "signup">("login")
  const [loading, setLoading] = useState(false)
  const [message, setMessage] = useState<string | null>(null)
  const [isError, setIsError] = useState(false)

  const [loginData, setLoginData] = useState({
    username: "",
    password: "",
  })

  const [signupData, setSignupData] = useState({
    full_name: "",
    email: "",
    username: "",
    password: "",
    confirmPassword: "",
  })

  // =========================
  // HANDLERS
  // =========================

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault()
    setMessage(null)
    setLoading(true)

    try {
      const res = await authAPI.login(loginData)
      setAuthToken(res.access_token)

      setIsError(false)
      setMessage("Login successful! Redirecting...")

      setTimeout(() => router.push("/dashboard"), 1200)
    } catch (err: any) {
      setIsError(true)
      setMessage(err.message || "Login failed")
    } finally {
      setLoading(false)
    }
  }

  const handleSignup = async (e: React.FormEvent) => {
    e.preventDefault()
    setMessage(null)

    if (signupData.password !== signupData.confirmPassword) {
      setIsError(true)
      setMessage("Passwords do not match")
      return
    }

    setLoading(true)

    try {
      await authAPI.signup({
        email: signupData.email,
        username: signupData.username,
        password: signupData.password,
        full_name: signupData.full_name,
      })

      setIsError(false)
      setMessage("Account created! You can now log in.")

      setTimeout(() => {
        setMode("login")
        setLoginData({
          username: signupData.username,
          password: signupData.password,
        })
      }, 1500)
    } catch (err: any) {
      setIsError(true)
      setMessage(err.message || "Signup failed")
    } finally {
      setLoading(false)
    }
  }

  // =========================
  // UI
  // =========================

  return (
    <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-indigo-100 via-blue-100 to-purple-100 px-4">
      <div className="w-full max-w-md bg-white rounded-2xl shadow-xl p-8 space-y-6">
        {/* Header */}
        <div className="text-center">
          <h1 className="text-3xl font-bold">
            {mode === "login" ? "Welcome Back ðŸ‘‹" : "Create Account ðŸš€"}
          </h1>
          <p className="text-gray-500 mt-1">
            {mode === "login"
              ? "Sign in to continue"
              : "Join us and start summarizing smarter"}
          </p>
        </div>

        {/* Message */}
        {message && (
          <div
            className={`flex items-center gap-2 p-3 rounded-lg text-sm ${
              isError
                ? "bg-red-100 text-red-700"
                : "bg-green-100 text-green-700"
            }`}
          >
            {isError ? (
              <AlertCircle className="w-5 h-5" />
            ) : (
              <CheckCircle className="w-5 h-5" />
            )}
            {message}
          </div>
        )}

        {/* Forms */}
        {mode === "login" ? (
          <form onSubmit={handleLogin} className="space-y-4">
            <Input
              label="Username"
              value={loginData.username}
              onChange={(e) =>
                setLoginData({ ...loginData, username: e.target.value })
              }
            />

            <Input
              label="Password"
              type="password"
              value={loginData.password}
              onChange={(e) =>
                setLoginData({ ...loginData, password: e.target.value })
              }
            />

            <SubmitButton loading={loading} text="Sign In" />
          </form>
        ) : (
          <form onSubmit={handleSignup} className="space-y-4">
            <Input
              label="Full Name"
              value={signupData.full_name}
              onChange={(e) =>
                setSignupData({ ...signupData, full_name: e.target.value })
              }
            />

            <Input
              label="Email"
              type="email"
              value={signupData.email}
              onChange={(e) =>
                setSignupData({ ...signupData, email: e.target.value })
              }
            />

            <Input
              label="Username"
              value={signupData.username}
              onChange={(e) =>
                setSignupData({ ...signupData, username: e.target.value })
              }
            />

            <Input
              label="Password"
              type="password"
              value={signupData.password}
              onChange={(e) =>
                setSignupData({ ...signupData, password: e.target.value })
              }
            />

            <Input
              label="Confirm Password"
              type="password"
              value={signupData.confirmPassword}
              onChange={(e) =>
                setSignupData({
                  ...signupData,
                  confirmPassword: e.target.value,
                })
              }
            />

            <SubmitButton loading={loading} text="Create Account" />
          </form>
        )}

        {/* Toggle */}
        <div className="text-center text-sm">
          {mode === "login" ? "No account?" : "Already have an account?"}{" "}
          <button
            onClick={() => setMode(mode === "login" ? "signup" : "login")}
            className="text-indigo-600 font-medium hover:underline"
            disabled={loading}
          >
            {mode === "login" ? "Sign up" : "Sign in"}
          </button>
        </div>
      </div>
    </div>
  )
}

// =========================
// REUSABLE COMPONENTS
// =========================

function Input({
  label,
  type = "text",
  ...props
}: React.InputHTMLAttributes<HTMLInputElement> & { label: string }) {
  return (
    <div>
      <label className="block text-sm font-medium mb-1">{label}</label>
      <input
        type={type}
        {...props}
        className="w-full rounded-lg border px-3 py-2 focus:ring-2 focus:ring-indigo-500 focus:outline-none"
      />
    </div>
  )
}

function SubmitButton({
  loading,
  text,
}: {
  loading: boolean
  text: string
}) {
  return (
    <button
      type="submit"
      disabled={loading}
      className="w-full bg-indigo-600 hover:bg-indigo-700 text-white rounded-lg py-2 font-semibold transition"
    >
      {loading ? (
        <span className="flex justify-center items-center gap-2">
          <Loader2 className="w-4 h-4 animate-spin" />
          Processing...
        </span>
      ) : (
        text
      )}
    </button>
  )
}
